body {
  font-family: Arial, sans-serif;
  background-color: #aad3f5;
  margin: 0;
  padding: 40px;
}

h1 {
  font-size: 28px;
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 30px;
}

input, select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  min-width: 150px;
}

button {
  padding: 8px 16px;
  border: none;
  background-color: #0066cc;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background-color: #004999;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
  box-shadow: 0 0 8px rgba(0,0,0,0.1);
}

th, td {
  padding: 12px 16px;
  border: 1px solid #ddd;
  text-align: left;
}

th {
  background-color: #f0f0f0;
}

tr:nth-child(even) {
  background-color: #fafafa;
}

.actions button {
  margin-right: 8px;
  background-color: #28a745;
}

.actions button:last-child {
  background-color: #dc3545;
}

.editing-note {
  color: #0057b7;
  font-weight: bold;
  margin-bottom: 10px;
}
